.main {
  padding-top: 60px;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 16px;
}

.list {
  @media screen and (max-width: 767px) {
    display: flex;
    flex-direction: column;
    align-items: center;

    .item {
      // width: 280px;
      margin-bottom: 20px;
    }
  }
}

@media screen and (min-width: 768px) {
  .list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .item {
    // width: 336px;
    margin-right: 30px;
    margin-bottom: 32px;

    // &:nth-child(2n) {
    //   margin-right: 30px;
    // }
  }
}

@media screen and (min-width: 1280px) {
  .list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  .item {
    width: calc((100% - 2 * 16px) / 3);
    margin-right: 16px;
    margin-bottom: 32px;

    &:nth-child(3n) {
      margin-right: 0;
    }
  }
}

.hero__title {
  color: black;
  text-align: center;
  display: none;
}

.photo__wrapper {
  border-radius: 5px;
  overflow: hidden;

  @media screen and (max-width: 767px) {
    width: 280px;
    height: 402px;
  }

  @media screen and (min-width: 768px) {
    width: 336px;
    height: 455px;
  }

  @media screen and (min-width: 1280px) {
    width: 395px;
    height: 574px;
  }
}

.img__link {
  display: block;

  .img {
    display: block;
    width: 100%;
    object-fit: cover;
  }
}

.data__content {
  padding-top: 12px;

  .title {
    color: black;
    font-size: 12px;
    line-height: 1.2;

    @media screen and (min-width: 768px) {
    }

    @media screen and (min-width: 1280px) {
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 1.2;
    }
  }

  .text {
    color: #ff6b08;
    font-size: 12px;
    line-height: 1.2;

    @media screen and (min-width: 768px) {
    }

    @media screen and (min-width: 1280px) {
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 1.2;
    }
  }
}
